.preview img {
    border-radius: 4px;
    filter: drop-shadow(0px 4px 5px #fff);
}

.preview img:hover {
    transition: all 0.1s;
    transform: translateY(-5px);
    filter: drop-shadow(0px 8px 8px #fff);
}

.preview img:active {
    transition: all 0.1s;
    transform: translateY(-1px);
    filter: drop-shadow(0px 8px 8px #fff);
}

.term {
    display: flex;
    flex-direction: column;
    /* width: 25%; */
    padding: 50px;
    margin: 40px;
    height: auto;
    overflow: hidden;
    background-color: #171717;
    text-align: center;
    border-radius: 24px;
    filter: drop-shadow(0px 4px 6px #000);
}

.title {
    margin-top: 20px;
    font-size: 24px;
    font-family: 'Press Start 2P', cursive;
}

.link {
    margin-top: 10px;
    font-family: 'Major Mono Display', monospace;
    line-height: 25px;
}